@import "../abstracts/all";

#item_list_table{
  a.btn,
  button.btn{
    margin: 2px 0;
  }
  .updated_by{
    font-size: 0.9em;
    line-height: 1.3em;
    margin-top: 3px;
  }
}

// ### IMET ###
.imet_modules{
  .field-edit,
  .field-enabled,
  .field-preview,
  .field-disabled{
    color: $darkestGray;
  }
}

.imet_name{
  display: flex;
  flex-direction: column;
  .imet_pa_name{
    font-size: 1.1em;
    flex-grow: 1;
  }
}

.imet_responsible{
  font-size: 0.85em;
  text-align: left;
  ul{
    margin: 0;
    padding-inline-start: 15px;
    li{
      line-height: 1.4em;
    }
  }
  button{
    margin-top: 5px;
  }
  .hidden{
    display: none !important;
  }
}

.histogram-row{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  .histogram-row__code{
    width: 45px;
  }
  .histogram-row__title{
    width: 340px;
    padding-left: 5px;
    padding-right: 5px;
  }
  .histogram-row__value{
    width: 45px;
  }
  .histogram-row__progress-bar{
    font-size: 10px;
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    .histogram-row__progress-bar__bar,
    .histogram-row__progress-bar__spacer{
      flex-grow: 1;
    }
    .histogram-row__progress-bar__limit-left{
      width: 30px;
      text-align: right;
      padding-right: 3px;
    }
    .histogram-row__progress-bar__limit-right{
      width: 30px;
      text-align: left;
      padding-left: 3px;
    }
  }
}



.progress_bar_label,
.progress_bar,
.progress_bar_container{
  padding: 0;
}

.progress_bar_limits{
  font-size: 10px;
  padding: 0 3px 0 0;
  text-align: right;
}
.progress_bar_limits_left{
  font-size: 10px;
  padding: 0 0 0 3px;
  text-align: left;
}
.progress{
  margin: 2px 0;
  vertical-align: bottom;
}
.progress-bar{
  color: #000000;
  font-weight: bold;
  -webkit-print-color-adjust: exact;
}
.progress-bar-negative{
  float: right;
  color: Red;
}
.synthetic_indicator{
  font-size: 1.2em;
}


section.convergence-plan div.card {
  span.checkbox {
    margin-left: 40px;
  }

  h5{
    margin: 20px 0 20px 15px;
    font-weight: bold;
    b {
      color: $contextual-primary;
    }
  }

  a {
    color: $baseGreen !important;
  }
  a.collapsed {
    color: $darkerGreen !important;
  }
  a:hover {
    text-decoration: none !important;
    color: black !important;
  }
  a:focus {
    text-decoration: none !important;
  }

  div.card-body h4 {
    margin-top: 15px;
    margin-bottom: 20px;
    border: solid 1px rgba(117, 111, 111, 0.4);
    padding: 5px;
    border-radius: 5px;
    background-color: $baseGray;
  }
}

#module_project__objective{

  .dark_background{
      background-color: #c2c2c2;
      margin-top: 0;
      margin-bottom: 25px;
      padding-bottom: 10px;
      padding-left: 20px;
  }
  .add_button_objectives{
      margin-bottom: 10px;
  }

  .project-general-objective{
      padding-bottom: 20px;
  }

  .project-result{
      padding-top: 20px;
  }
  .project-activity{
      padding-top: 10px;
  }

  .project-product{
    padding-top: 10px;
  }

}

#module_project__objective_monitoring {

  .dark_background{
    background-color: #c2c2c2;
  }

  .objective_container{
    margin: 0 10px;

    .input-textarea{
      padding-right: 0;
    }

    .progress{
      margin-bottom: 10px;
      padding: 0;
      .general-progress{
        background-color: $baseGreen;
      }
      .specific-progress{
        background-color: #255482;
      }
      .result-progress{
        background-color: #2f6ba4;
      }
      .activity-progress{
        background-color: #3788ca;
      }
      .product-progress{
        background-color: #3b9be1;
      }
    }
  }

}