.switch_checkbox input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}
.switch_checkbox label {
  cursor: pointer;
  text-indent: -9999px;
  background: grey;
  display: block;
  border-radius: 100px;
  position: relative;
  margin: 0;
}
.switch_checkbox label:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  background: #fff;
  border-radius: 50%;
  transition: 0.3s;
}
.switch_checkbox input:checked + label {
  background: #00A74B;
}
.switch_checkbox input:checked + label:after {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}
.switch_checkbox label:active:after {
  width: 20px;
}
.switch_checkbox label {
  width: 30px;
  height: 15px;
}
.switch_checkbox label:after {
  width: 11px;
  height: 11px;
}
.switch_checkbox.sm label {
  width: 22px;
